.blackout {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  background-color: $color-background-blackout;
  cursor: pointer;
  @include animation();

  &._active {
    visibility: visible;
    opacity: 1;
  }
}
